@use "global" as *;

.p-blog {

}

.p-blog__inner {
  position: relative;
}

.p-blog__title {
  width: 100%;
  display: inline-block;
  text-align: center;
}

.p-blog__content {
  overflow: visible;
}

.p-blog__button-box {
  @include mq(md) {
    position: absolute;
    display: flex;
    gap: myrem(80);
    top: 0;
    right: 12.5%;
  }
}

.p-blog__button-prev,
.p-blog__button-next {
  display: none;
  @include mq(md) {
    display: block;
    width: myrem(80);
    height: myrem(80);
    z-index: 9999;
  }
}
.p-blog__button-prev:hover,
.p-blog__button-next:hover {

  @include mq(md) {
    cursor : pointer;
    opacity: 0.7;
  }
}

.p-blog__button-prev {
  @include mq(md) {
    right: 23.8%;
    top: myrem(-67.2);
  }
}

.p-blog__button-next {
  @include mq(md) {
    right: 12.6%;
    top: myrem(-69);
 }
}

.p-blog__button-prev::before,
.p-blog__button-next::before {
  content: "";
  display: inline-block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}

.p-blog__button-prev:before {
  background-image: url(/wp-content/themes/WordPressTheme/assets/images/common/dummy.jpg);
}

.p-blog__button-next:before {
  background-image: url(/wp-content/themes/WordPressTheme/assets/images/common/dummy.jpg);
}

.p-blog__button-prev:after,
.p-blog__button-next:after {
  display: none;
}

.p-blog__swiper {
  margin-top: myrem(24);
  max-width: 100%;
  padding-bottom: myrem(10);
  @include mq(md) {
   margin-top: myrem(40);
  }  
}

.p-blog__swiper-slide {
  max-width: myrem(280);
  @include mq(md) {
    max-width: myrem(333);
    min-width: myrem(333);
    width: 100%;
    height: auto;
  }
}

.p-blog__slide-card {
  max-width: myrem(280);
  height: auto;
  width: 100%;
  @include mq(md) {
    max-width: 100%;
    // width: 100%;
    // width: myrem(333);
  }
}

.swiper-slide {
  height: auto;
  width: myrem(333);
} 

.p-blog__btn {
  text-align: center;
  margin-top: myrem(32);
  padding-bottom: myrem(5);
  @include mq(md) {
    margin-top: myrem(70);
  }
}


