.c-button {
  display: inline-block;
  background: #1a1917;
  color: #fff;
  border: none;
  position: relative;
  padding: 1rem 2.5rem;
  min-width: 15rem;
  font-size: 1rem;
  cursor: pointer;
  transition: 800ms ease all;
  outline: none;
  text-align: center;
}

.c-button::first-letter {
  text-transform: capitalize;
}

.c-button.c-button--white {
  background: #fff;
  color: #1a1917;
}

.c-button:hover {
  background: #fff;
  color: #1a1917;
}

.c-button.c-button--white:hover {
  color: #fff;
  background: #1a1917;
}

.c-button:before,
.c-button:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #1a1917;
  transition: 400ms ease all;
}

.c-button.c-button--white:before,
.c-button.c-button--white:after {
  background: #fff;
}

.c-button:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}

.c-button:hover:before,
.c-button:hover:after {
  width: 100%;
  transition: 800ms ease all;
}

